.section-welcome {
  width: 100%;
  height: 70vh;
  position: relative;
  .content-bg-welcome {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url(../images/bg-welcome.png);
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    clip-path: polygon(0 0, 100% 0%, 100% 80%, 50% 100%, 0 80%);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    h1 {
      // font-family: 'Lobster';
      color: $white;
      font-size: 3.5rem;
      text-shadow: 0 0 10px $black;
      font-weight: 600;
    }
    h2 {
      // font-family: 'Lobster';
      color: $white;
      font-size: 1.8rem;
      text-shadow: 0 0 10px $black;
      font-style: italic;
    }
    .triangle {
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 25vh;
      left: 0;
      right: 0;
      clip-path: polygon(50% 80%, 100% 40%, 100% 100%, 0 100%, 0% 40%);
      background: radial-gradient(circle, $gray-light 0%, $blue 50%, $blue 100%);
    }
  }
  .content-img-perfil {
    width: 35vh;
    height: 35vh;
    border-radius: 50%;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 40%);
    background-color: $white;
    box-shadow: 0px 0px 10px $text-gray;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      border-radius: 50%;
      transform: scale(0.96);
    }
  }
}