.proximamente-modal {
  text-align: center;
}
.section-cursos {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .container {
    .proximamente {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      border-radius: 15px;
      font-weight: bold;
      padding: 0.3rem;
    }
    .content-cards {
      .card-col {
        padding-top: 1rem;
        padding-bottom: 1rem;
        @media (min-width: 1200px) {
          padding-top: 3rem;
        }
        .item-card {
          position: relative;
          width: 100%;
          border: none;
          border-radius: 15px;
          text-align: center;
          .img-card {
            width: 100%;
            height: auto;
            border: none;
            border-radius: 15px;
            transition: all 0.3s;
          }
          .btn {
            border: 1px solid $blue;
            border-radius: 50px;
            padding: 1rem 1.5rem;
            font-size: 1rem;
            font-weight: 600;
            font-style: italic;
            background-color: $blue;
            color: $white;
            text-align: center;
            transition: all 0.3s;
            margin-top: 1rem;
            &:hover {
              background-color: $white;
              color: $blue;
              box-shadow: 0 0 5px $white;
            }
          }
        }
      }
    }
  }
}