.section-curriculum {
  padding-top: 3rem;
  padding-bottom: 3rem;
  .container {
    .content-cards {
      .card-col {
        padding-top: 1rem;
        padding-bottom: 1rem;
        @media (min-width: 1200px) {
          padding-top: 3rem;
          padding-bottom: 3rem;
          &:nth-child(2n+1) {
            padding-right: 3rem;
          }
          &:nth-child(2n) {
            padding-left: 3rem;
          }
        }
        .item-card {
          position: relative;
          width: 100%;
          padding-bottom: 60%;
          border: none;
          border-radius: 15px;
          &:hover {
            .img-card {
              transform: scale(1.05);
            }
          }
          .img-card {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border: none;
            border-radius: 15px;
            object-fit: cover;
            object-position: center;
            transition: all 0.3s;
          }
          .btn-group-custom {
            position: absolute;
            bottom: 1rem;
            left: 1rem;
            border: none;
            border-radius: 50px;
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            padding: 1rem 0;
            font-size: 1.5rem;
            transition: all 0.3s;
            .btn-text {
              padding: 0 6rem 0 2rem;
              color: $text-gray;
              font-style: italic;
              font-weight: 600;
              transition: all 0.3s;
            }
            .btn-icon {
              padding: 0 1.5rem;
              background-color: $blue;
              border-bottom-right-radius: 50px;
              border-top-right-radius: 50px;
              height: 100%;
              position: absolute;
              right: 0;
              top: 0;
              display: flex;
              align-items: center;
              color: $white;
              transition: all 0.3s;
            }
            &:hover {
              background-color: $blue;
              box-shadow: 0 0 5px $white;
              .btn-text {
                color: $white;
              }
              .btn-icon {
                background-color: $white;
                color: $blue;
              }
            }
          }
        }
      }
    }
  }
}