.section-perfil {
  padding-top: 20vh;
  padding-bottom: 3rem;
  .col-izq {
    @media (max-width: 991.98px) {
      box-shadow: 2px 52px 19px -56px rgba(0,0,0,0.75);
      padding-bottom: 3rem;
    }
  }
  .col-der {
    text-align: center;
    @media (min-width: 992px) {
      box-shadow: -35px 0px 16px -41px rgba(0,0,0,0.75);
    }
    .content-icons {
      align-items: center;
      margin-top: 3rem;
      .fa-stack {
        font-size: 1.2rem;
        .icon-bg {
          color: $blue;
          transition: all 0.3s;
        }
        .icon-red {
          color: $white;
          transition: all 0.3s;
        }
      }
    }
    .enlace-perfil {
      text-decoration: none;
      color: $text-gray;
      transition: all .3s;
      &:hover {
        color: $blue;
      }
    }
  }
}